<template>
<v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
            <v-btn icon dark v-on="on">
              <v-icon>add</v-icon>
            </v-btn>

      </template>
      <v-card>
        <v-card-text>
          <v-container grid-list-sm>
            <v-layout wrap>
              <v-flex xs12>
                <v-card-title>
                  <span class="headline">Launch Job</span>
                </v-card-title>
              </v-flex>

              <v-flex xs12>
                <v-select
                  v-model="selected_job"
                  :items="items"
                  item-text="job_name"
                  label="Jobs"
                  return-object
                  required
                ></v-select>
              </v-flex>
              <v-flex xs12>
                <v-textarea
                  outline
                  v-model="job_route_data"
                  label="data"
                  >
                </v-textarea>
              </v-flex>
              <v-flex xs12 md3 lg3>
                    <v-checkbox v-model="advanced_checkbox" :label="`Advance`"></v-checkbox>
              </v-flex>
              <v-flex xs12 md9 lg9>
                <v-text-field
                  label="Task Route"
                  v-model="advanced_task_route"
                  placeholder="Advance (not needed)"
                  :disabled="!advanced_checkbox"
                  outline
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog = false">Cancel</v-btn>
          <v-btn color="blue darken-1" flat @click="clicked_launch">Launch</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>
<script>
export default {
  methods: {
    clicked_launch () {

      var job_route_data = JSON.parse(this.job_route_data)
      if (job_route_data) { //checks for valid json
      console.log("SDS")
        var route = null
        var job_name = null
        var task_name = null



        if (this.advanced_checkbox) {
          route = this.advanced_task_route 
          job_name = "Anon Job: " + route
        }
        else {
          route = this.selected_job.route
          job_name = this.selected_job.job_name
        }

        task_name = 'Launcher'//route 

        var msg = {
          route: "bd.@md.slave.job.add",
          data: {
            job_data: {
              name: job_name
            },
            tasks_data: [
                {
                  name: task_name,
                  route: route,
                  data: job_route_data 
                }
              ]
          }
        }
        console.log("JOB LAUNCH MSG: "+JSON.stringify(msg))
        var s = this.$socket.emit('forward', msg)
        if (s.connected) this.dialog = false
      }
    }
  },
  data: () => {
    return {
      advanced_checkbox: false,
      advanced_task_route: '',
      job_route_data: '{}',
      selected_job: null,
      items: [
        {
          job_name: 'Swing Trading',
          route: 'bd.sd.@BTV1.trade.swing',
          rdata: {},
          value: '1'
        },
        {
          job_name: 'Create new instagram bots',
          route: 'bd.sd.@IGFV1.^bot.users.create',
          rdata: {'msgs': [{"msg": 4}, {"msg":5}, {"msg":6}]},
          value: '2'
        }
      ],
      dialog: false
    }
  }
}
</script>

